.section-card {
  margin: 20px;
  padding: 20px;
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s ease-in-out;
}

.section-card:hover {
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
}

.card-content {
  margin-top: 10px;
}

.section-card h2 {
  color: #03adfc;
  transition: letter-spacing 0.3s ease, text-shadow 0.3s ease;
}

.section-card h2:hover {
  text-shadow: 2px 2px 2px #9ee0ff;
  letter-spacing: 1px;
}
